import React from 'react';
import {render} from 'react-dom';
import WeekView from './weekview.jsx';
import DayView from './dayview.jsx';

var PopupApp = React.createClass({
	getInitialState: function(){
		return {mode:"WEEK"};
	},
	setMode: function(m){
		this.setState({mode:m})
	},
	render: function(){

		var lowerContent;
		if(this.state.mode == "WEEK"){lowerContent = (<WeekView/>)}
		else { lowerContent = (<DayView/>)};

		return(<div>
		<NavBar mode = {this.state.mode} setMode = {this.setMode}/>
		{lowerContent}
		</div>);
	}
})

var NavBar = React.createClass({
	render: function(){
		return(
			<div>
				<div onClick = {function(){this.prop.setMode("WEEK")}}>Week</div>
				<div onClick = {function(){this.prop.setMode("DAY")}}>Day</div>
				<div>More</div>
			</div>
		);
	}
})


render(<PopupApp/>, document.getElementById('app'));


